body {
  color: #333;
  background-color: #f5f5f5;
  margin: 0;
  font-family: Arial, sans-serif;
}

.header {
  background-color: #e0e0e0;
  border-bottom: 1px solid #ccc;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.create-theme-btn {
  color: #333;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px 20px;
  transition: background-color .3s, color .3s;
}

.create-theme-btn:hover {
  color: #000;
  background-color: #ddd;
}

.post {
  background-color: #e0e0e0;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 20px;
  padding: 20px;
}

.post-title {
  margin-bottom: 10px;
  font-size: 24px;
}

.post-content {
  margin-bottom: 20px;
  font-size: 16px;
}

.post-actions {
  width: 150px;
  justify-content: space-between;
  display: flex;
}

.post-comment-btn, .post-delete-btn, .post-edit-btn {
  color: #333;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 5px 10px;
  transition: background-color .3s, color .3s;
}

.post-comment-btn:hover, .post-delete-btn:hover, .post-edit-btn:hover {
  color: #000;
  background-color: #ddd;
}

.comments {
  border-left: 2px solid #ccc;
  margin-top: 20px;
  padding-left: 20px;
}

.comment {
  background-color: #f9f9f9;
  border-radius: 3px;
  margin-bottom: 10px;
  padding: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

ul {
  list-style: none;
}

